<template>
    <div class="about">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png">
            </span>
            <span class="usersTitle">关于我们</span>
            <span class="back"></span>
        </div>
        <div class="content">
            <div class="appLogo">
                <img src="../../assets/img/appLogo.png">
            </div>
            <div class="appName">哼哈太极</div>
            <div class="version">v1.0</div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'about',
  methods: {
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
 * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .about {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .content {
    flex-grow: 1;
    background: #f5f5f5;
  }

  .appLogo {
    width: 1.8rem;
    height: 1.8rem;
    margin: 0 auto;
    margin-top: 3.6rem;
  }

  .appName {
    color: #333333;
    margin-top: 0.5rem;
  }

  .version {
    color: #666666;
  }
</style>



